*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.container-sm {
  max-width: 70rem;
  margin: 0 auto;
}

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
.loading__title {
  font-size: 2rem;
  text-align: center;
  color: #fff;
}
.loading__spinner {
  width: 48px;
  height: 48px;
  border: 5px solid #fff;
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: spinner_loading 1s linear infinite;
}

@keyframes spinner_loading {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
h1,
h2,
h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  color: #6b7280;
  line-height: 1.8;
  margin: 0%;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: bold;
  color: #000;
  font-size: clamp(1.5rem, 4vw, 3rem);
  text-align: center;
}

/** Media Queries **/
@media (prefers-color-scheme: dark) {
  body {
    background-color: #2c2c3b;
  }
  .tagline,
  .form__field,
  .login .links a,
  .register .links a,
  .forgot-password .links a {
    color: #ffffff;
  }
  .descripcion-pagina {
    color: #ffffff;
  }
  .formulario input {
    background-color: #37374a;
  }
  .dashboard {
    background-color: #2c2c3b;
  }
  .dashboard .nombre-pagina {
    color: #ffffff;
  }
  .sidebar {
    background-color: #2c2c3b;
  }
  .barra {
    background-color: #21212c;
  }
  .barra p {
    color: #ffffff;
  }
  .barra p span {
    color: #ffffff;
  }
  .listado-proyectos .proyecto {
    background-color: #585875;
  }
  .listado-proyectos .proyecto:hover {
    background-color: #484861;
  }
  .filtros {
    background-color: #424258;
  }
  .filtros .filtros-inputs h2,
  .filtros .filtros-inputs label {
    color: #ffffff;
  }
  .listado-tareas .tarea {
    background-color: #424258;
    border: none;
  }
  .listado-tareas .tarea p {
    color: #bac0ca;
  }
  .modal .formulario.animar {
    background-color: #424258;
  }
  .modal .formulario.animar legend {
    color: #ffffff;
  }
}
.layout__auth {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 5rem 0;
}
.layout__container {
  padding: 2rem;
}
.layout__project {
  background-color: #2c2c3b;
  color: #ffffff;
}
.layout__project--container {
  display: flex;
  min-height: calc(100vh - 6rem);
}

.alerts {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  font-weight: 900;
  margin: 0rem auto 1rem;
  font-size: 1.2rem;
  max-width: 80rem;
}
.alerts.error {
  background-color: #ffa9a9;
  border-left: 0.7rem solid rgb(169, 0, 0);
}
.alerts.success {
  background-color: #78ff78;
  border-left: 0.7rem solid rgb(0, 120, 0);
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}
.header .title {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.header .title__menu {
  width: 3.2rem;
  height: 3.2rem;
  padding: 0.5rem;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}
.header .title__menu:hover {
  background-color: #6b7280;
}
.header .title__name {
  display: flex;
  align-items: center;
  gap: 0.2rem;
  font-size: 2.2rem;
  font-weight: 700;
  margin: 0;
}
.header .title__name--icon {
  width: 2rem;
  height: 2rem;
}
.header__right {
  display: flex;
  align-items: center;
  font-size: 1.4rem;
  font-weight: 500;
}
.header .user {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.header .user__name {
  font-size: 1.4rem;
  font-weight: 700;
  margin: 0;
}
.header .user__img {
  width: 4rem;
  height: 4rem;
  padding: 0.7rem;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}
.header .user__img:hover {
  background-color: #6b7280;
}

.sidebar {
  background-color: inherit;
  max-width: 22rem;
  width: 100%;
  padding: 0 1rem;
}
.sidebar__nav {
  border-bottom: 0.1rem solid #bac0ca;
  padding-bottom: 0.5em;
}
.sidebar__link {
  display: flex;
  align-items: center;
  font-family: Arial, Helvetica, sans-serif;
  color: #ffffff;
  font-weight: 500;
  font-size: 1.4rem;
  margin-bottom: 0.5rem;
  padding: 1.2rem 1.8rem;
  border-radius: 1rem;
  transition: background-color 300ms ease;
}
.sidebar__link:hover, .sidebar__link.active {
  background-color: #2563eb;
}
.sidebar__link--icon {
  width: 2rem;
  height: 2rem;
  margin-right: 1rem;
}
.sidebar__link-logout {
  color: #ffffff;
  transition: background-color 300ms ease;
}
.sidebar__link-logout:hover {
  background-color: #ff0000;
}
.sidebar__menu {
  margin-top: 1rem;
}

.form {
  max-width: 60rem;
  width: 100%;
  margin: 1rem auto;
}
.form__field {
  display: flex;
  flex-direction: column;
  padding-bottom: 2rem;
}
.form__field--label {
  color: #ffffff;
  font-size: 1.3rem;
  padding-bottom: 0.5rem;
  font-weight: 600;
}
.form__field--input {
  border-radius: 0.5rem;
  padding: 1rem;
  border: none;
  outline: none;
  background-color: #d8dadf;
}
.form__submit {
  background-color: #0891b2;
  color: #ffffff;
  display: block;
  margin-top: 2rem;
  border: none;
  border-radius: 0.5rem;
  width: 100%;
  padding: 1.3rem 0;
  text-align: center;
  cursor: pointer;
  font-weight: 700;
  transition-property: background-color;
  transition-duration: 0.3s;
}
.form__submit:hover {
  background-color: #066981;
  cursor: pointer;
}

.links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 0;
}
@media (max-width: 580px) {
  .links {
    flex-direction: column;
    gap: 0.5rem;
  }
}
.links__link {
  all: unset;
  color: #000;
  font-weight: bold;
  cursor: pointer;
}
.links__link--text {
  color: #ffffff;
  font-size: 1.6rem;
}

.login {
  max-width: 60rem;
  width: 95%;
}
.login .title {
  background: linear-gradient(to right, #0891b2 0%, #5bd9f8 50%, #bdf0fc 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: clamp(5rem, 12vw, 10rem);
  margin: 0%;
}
.login .form__submit {
  background-color: #0891b2;
}
.login .links__link {
  color: #0891b2;
}

.register {
  max-width: 60rem;
  width: 95%;
}
.register .title {
  background: linear-gradient(to right, #4338ca 0%, #4338ca 50%, white 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: clamp(5rem, 12vw, 10rem);
  margin: 0%;
  margin-bottom: 0%;
}
.register .form__submit {
  background-color: #4338ca;
}
.register .links__link {
  color: #4338ca;
}

.forgot-password {
  max-width: 60rem;
  width: 95%;
}
.forgot-password .title {
  background: linear-gradient(to right, #2563eb 0%, #2563eb 50%, white 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: clamp(5rem, 12vw, 10rem);
  margin: 0%;
}
.forgot-password .form__submit {
  background-color: #2563eb;
}
.forgot-password .links__link {
  color: #2563eb;
}

.reset-password {
  max-width: 60rem;
  width: 95%;
}
.reset-password .title {
  background: linear-gradient(to right, #ff0000 0%, #ff0000 50%, white 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: clamp(5rem, 12vw, 10rem);
  margin: 0%;
}
.reset-password .form__submit {
  background-color: #ff0000;
}
.reset-password .links__link {
  color: #ffffff;
}
.reset-password .links__link--text {
  color: #bac0ca;
}

.message .title {
  background: linear-gradient(to right, #f59e0b 0%, #f59e0b 50%, white 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: clamp(5rem, 12vw, 10rem);
  margin: 0%;
}
.message__description {
  font-weight: 600;
  color: #bac0ca;
}

.confirm-account {
  max-width: 60rem;
  width: 95%;
}
.confirm-account .title {
  background: linear-gradient(to right, #f59e0b 0%, #f59e0b 50%, white 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: clamp(5rem, 12vw, 10rem);
  margin: 0%;
}
.confirm-account .links__link {
  background-color: #f59e0b;
  color: #ffffff;
  display: block;
  margin-top: 2rem;
  border: none;
  border-radius: 0.5rem;
  width: 100%;
  padding: 1.3rem 0;
  text-align: center;
  cursor: pointer;
  font-weight: 700;
  transition-property: background-color;
  transition-duration: 0.3s;
}
.confirm-account .links__link:hover {
  background-color: #c57f08;
  cursor: pointer;
}

.project {
  flex: 1;
  overflow-y: auto;
}
.project__container {
  min-height: calc(100vh - 6rem);
  padding: 0 2rem 0 4.5rem;
}
.project__title {
  font-size: 2.5rem;
  margin: 0%;
}
.project__empty {
  color: #ffffff;
}
.project__list {
  list-style: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}
.project__link {
  background-color: #564ccf;
  color: #ffffff;
  font-weight: 700;
  display: flex;
  align-items: center;
  min-height: 10rem;
  line-height: 2;
  border-radius: 0.5rem;
  transition: background-color 400ms ease;
}
.project__link:hover {
  background-color: #3b31b8;
}
.project__link--content {
  display: block;
  width: 100%;
  height: 100%;
  color: #ffffff;
  font-weight: 900;
  padding: 1rem;
}

