}
.createProject .form__submit {
  background-color: #2563eb;
  color: #ffffff;
  display: block;
  margin-top: 2rem;
  border: none;
  border-radius: 0.5rem;
  width: 100%;
  padding: 1.3rem 0;
  text-align: center;
  cursor: pointer;
  font-weight: 700;
  transition-property: background-color;
  transition-duration: 0.3s;
}
.createProject .form__submit:hover {
  background-color: #134cca;
  cursor: pointer;
}

.changePassword__container {
  max-width: 60rem;
  width: 100%;
}
.changePassword__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}
.changePassword__title {
  font-size: 2.5rem;
  margin: 0%;
}
.changePassword__link {
  color: #2563eb;
  font-size: 1.5rem;
  text-decoration: none;
}
.changePassword__link:hover {
  text-decoration: underline;
}

.profile__container {
  max-width: 60rem;
  width: 100%;
}
.profile__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}
.profile__title {
  font-size: 2.5rem;
  margin: 0%;
}
.profile__link {
  color: #2563eb;
  font-size: 1.5rem;
  text-decoration: none;
}
.profile__link:hover {
  text-decoration: underline;
}

.barra {
  background-color: #ffffff;
  padding: 1rem 2rem;
  box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.barra p {
  margin: 0;
  font-size: 2rem;
}
.barra p span {
  font-weight: 700;
  color: #4338ca;
}
.barra .salir {
  background-color: #352ca3;
  padding: 1.5rem 4rem;
  color: #ffffff;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .barra .salir {
    width: auto;
  }
}
.barra .salir:hover {
  background-color: #28217b;
  cursor: pointer;
}

.contenedor-nueva-tarea .agregar-tarea {
  background-color: #0891b2;
  padding: 1.5rem 4rem;
  color: #ffffff;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contenedor-nueva-tarea .agregar-tarea {
    width: auto;
  }
}
.contenedor-nueva-tarea .agregar-tarea:hover {
  background-color: #066981;
  cursor: pointer;
}

.list__tasks {
  list-style: none;
  margin-top: 3rem;
}
.list__tasks .task {
  padding: 0.5rem 2rem;
  border-bottom: 0.2rem solid #808b9e;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.list__tasks .task:last-of-type {
  border: none;
}
.list__tasks .task__name {
  font-size: 1.7rem;
}
.list__tasks .task__empty {
  text-align: center;
  color: #ffffff;
  font-weight: 700;
}
.list__tasks .task__options {
  display: flex;
  gap: 1.5rem;
}
.list__tasks .task__status {
  border: none;
  font-size: 1rem;
  padding: 0.8rem 1rem;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 1rem;
  cursor: pointer;
}
.list__tasks .task__status.completed {
  background-color: #2563eb;
  color: #ffffff;
}
.list__tasks .task__status.pending {
  background-color: #f59e0b;
  color: #ffffff;
}
.list__tasks .task__delete {
  border: none;
  font-size: 1rem;
  padding: 0.8rem 1rem;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 1rem;
  cursor: pointer;
  background-color: rgb(169, 0, 0);
  color: #ffffff;
}

.modal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.6);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.modal__hidden {
  display: none;
}
.modal__form {
  max-width: 60rem;
  background-color: #ffffff;
  padding: 2rem;
  margin: 0 auto;
  transition: all 500ms ease-in-out;
  transform: translateY(-30rem);
  opacity: 0;
  border-radius: 1rem;
}
.modal__form--show {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.modal__form--hide {
  transform: translateY(-200%);
  opacity: 0;
}
.modal__header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.modal__title {
  margin: 0;
}
.modal__close {
  border: none;
  outline: none;
  border-radius: 50%;
  background-color: red;
  color: #ffffff;
  padding: 0.5rem 1rem;
  font-size: 2rem;
  cursor: pointer;
}
.modal__body {
  margin: 2rem 0;
}
.modal__field {
  width: 100%;
  display: flex;
  align-items: center;
}
.modal__field--name {
  max-width: 10rem;
  width: 100%;
  font-weight: 700;
}
.modal__field--value {
  width: 100%;
  padding: 1.2rem 1rem;
  border: 0.1rem solid #bac0ca;
  border-radius: 0.5rem;
  outline: none;
  font-size: 1.6rem;
}
.modal__submit {
  background-color: #4338ca;
  padding: 1.5rem 4rem;
  color: #ffffff;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .modal__submit {
    width: auto;
  }
}
.modal__submit:hover {
  background-color: #352ca3;
  cursor: pointer;
}

.filtros {
  background-color: #ffffff;
  margin-top: 2rem;
  border-radius: 1rem;
  padding: 2rem;
}
.filtros .filtros-inputs {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.filtros .filtros-inputs h2 {
  font-size: 3rem;
  color: #4338ca;
  margin: 0;
}
.filtros .filtros-inputs .campo {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}
.filtros .filtros-inputs .campo input[type=radio] {
  accent-color: #4338ca;
  width: 2rem;
  height: 2rem;
}